@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@700;800;900&display=swap);

.App-header {
  padding: 0 2vw;
  min-height: 100vh;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/* Link Style 1 */
a {
  border-bottom: 0.05em solid #0df2c9;
  box-shadow: inset 0 -0.05em 0 #0df2c9;
  color: inherit;

  -webkit-transition: all 0.25s cubic-bezier(0.33, 0.66, 0.66, 1);

  transition: all 0.25s cubic-bezier(0.33, 0.66, 0.66, 1);
  text-decoration: none;
  padding-left: 0px;
  padding-right: 0px;
}

a:hover,
a:focus,
a:active {
  color: white;
  padding-left: 3px;
  padding-right: 3px;
  box-sizing: border-box;
  background-color: #0df2c9;
}

* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

body {
  font-family: -apple-system, system-ui, sans-serif;
  font-size: 1.125em;
  font-weight: 400;
  line-height: 150%;
  color: rgba(0, 0, 0, 0.75);
}

@-webkit-keyframes changewidth {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}

@keyframes changewidth {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}

.transition {
  display: inline-block;
  position: absolute !important;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  -webkit-transition-property: opacity, -webkit-transform;
  transition-property: opacity, -webkit-transform;
  transition-property: opacity, transform;
  transition-property: opacity, transform, -webkit-transform;
}

.transition.vertical[hidden] {
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
  opacity: 0;
  -webkit-transform: translate3d(0, -20px, 0) !important;
          transform: translate3d(0, -20px, 0) !important;
}

.transition.horizontal[hidden] {
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
  opacity: 0;
  -webkit-transform: translate3d(30px, 0, 0) !important;
          transform: translate3d(30px, 0, 0) !important;
}

.scrollArea {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: auto;
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto;
  grid-gap: 3rem;
  gap: 3rem;
  grid-template-areas: "left right" "content -";
}

.content {
  grid-area: content;
  overflow: hidden;
}

.grid-left {
  grid-area: left;
  background: #fdfabb;
  border-radius: 5px;
  padding: 2rem;
  cursor: pointer;
  overflow: hidden;
}

.grid-right {
  grid-area: right;
  border-radius: 5px;
  padding: 2rem;
  overflow: hidden;
  cursor: pointer;
}

